.usersPage {
    margin: 15px;
    color: white;
    text-align: center;
    position: relative;
}

.preloader {
    position: fixed;
    right: 142px;
    top: 55px;
    height: 45px;
}

.paginator {
    position: fixed;
    top: 110px;
    right: 45px;
    text-align: left;
}

.paginator:not(.paginator__link) > span::before {
    font-family: FontAwesome;
    content: '\f060';
    color: rgb(41, 94, 170);
    padding-right: 5px;
    cursor: pointer;
}

.paginator__link {
    right: 200px;
}

.paginator__link::before {
    font-family: FontAwesome;
    content: '\f0cb';
    color: rgb(41, 94, 170);
    padding-right: 5px;
    cursor: pointer;
}

.showHideLink {
    margin: 0;
    font-size: 18px;
    color: rgb(41, 94, 170);
    cursor: pointer;
}

.lastPage {
    font-size: 1.1em;
    font-style: italic;
    color: maroon;
    background-color: #f0f0f0;
    padding: 5px;
    border: 1px solid gray;
    border-radius: 10px;
}